import React, { Component } from 'react';
import styled, { css } from 'styled-components';

import icons from '../img';

import Projects from './Projects';

const Container = styled.div`
  height: 200vh;
  width: 100vw;
  overflow-x: hidden;
  background: #210d5d;
`;

const Page = styled.div`
  width: 100vw;
  height: 50%;
`;

const PortfolioPage = Page.extend``;

const Intro = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  position: relative;
`;

const Title = styled.div`
  font-size: 2em;
  text-align: right;
  color: #fff;
  flex-basis: 50%;
`;

const TitleFollowUp = Title.extend`
  position: absolute;
  left: 50%;
  color: ${props => props.color};
  opacity: 0;
  overflow: hidden;
  transition: position 0s linear 1s, opacity 1s linear;
  text-align: left;

  ${props =>
    props.selected &&
    css`
      opacity: 1;
      position: static;
    `};
`;

const ArrowIcon = styled.img`
  position: absolute;
  width: 60px;
  bottom: 3em;
  left: 50%;
  margin-left: -30px;
  cursor: pointer;
  transform: rotate(90deg);

  ${props =>
    props.selected &&
    css`
      transform: rotate(180deg);
    `};
`;

const words = [
  { name: 'Developer', color: '#5dffb9' },
  { name: 'Photographer', color: '#26C6DA' },
  { name: 'Learner', color: '#E040FB' }
];

class App extends Component {
  state = {
    wordIndex: 0,
    page: 'home'
  };

  constructor(props) {
    super(props);

    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    setInterval(() => {
      let nextIndex = this.state.wordIndex + 1;
      if (nextIndex > words.length - 1) {
        nextIndex = 0;
      }
      this.setState({ wordIndex: nextIndex });
    }, 2500);

    window.addEventListener('scroll', this.handleScroll, true);
  }

  handleScroll(e) {
    const { page } = this.state;
    if (page === 'home' && window.innerWidth / 2 < window.pageXOffset) {
      this.setState({ page: 'portfolio' });
    } else if (
      page === 'portfolio' &&
      window.innerWidth / 2 > window.pageXOffset
    ) {
      this.setState({ page: 'home' });
    }
  }

  render() {
    const { wordIndex, page } = this.state;

    return (
      <Container onScroll={this.handleScroll}>
        <Page selected={page === 'home' ? true : false}>
          <Intro>
            <Title>Arnaud</Title>
            {words.map(word => {
              return (
                <TitleFollowUp
                  key={word.name}
                  color={word.color}
                  selected={words.indexOf(word) === wordIndex ? true : false}
                >
                  {word.name}
                </TitleFollowUp>
              );
            })}
          </Intro>
          <ArrowIcon
            onClick={() => {
              if (page === 'home') {
                window.scrollTo(0, window.innerHeight);
              } else {
                window.scrollTo(0, 0);
              }
            }}
            src={icons.roundArrowForward}
            alt="Round arrow bottom"
            selected={page === 'portfolio' ? true : false}
          />
        </Page>
        <PortfolioPage selected={page === 'portfolio' ? true : false}>
          <Projects />
        </PortfolioPage>
      </Container>
    );
  }
}

export default App;



