import React from 'react';
import styled from 'styled-components';

import { ColorLuminance } from '../lib';

const Container = styled.ul`
  list-style: none;
  margin: 0 2em;
  padding-top: 2em;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const Project = styled.li`
  position: relative;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
  border-radius: 4px;
  height: 10em;
  width: 32%;
  margin: 0.7em 0;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.125);

  @media (min-width: 980px) {
    width: 24%;
  }

  @media (max-width: 400px) {
    width: 100%;
  }

  @media (max-width: 650px) {
    width: 48%;
  }
`;

const ProjectContainer = styled.div`
  padding: 0.7em;
  background: ${props => props.color};
  flex-grow: 1;
`;

const Title = styled.h3`
  margin: 0;
  color: ${props => props.color};
`;

const Text = styled.p`
  color: #fff;
  font-size: 0.8em;
`;

const Actions = styled.div`
  width: 100%;
  display: flex;
  line-height: 2.5em;
  text-align: center;
  text-transform: uppercase;
  font-size: 0.8em;
  background: ${props => ColorLuminance(props.color, -0.15)};

  & {
    a {
      text-decoration: none;
      width: 50%;
      color: #fff;
    }

    a:hover {
      background: ${props => ColorLuminance(props.color, -0.5)};
    }
  }
`;

const Filler = styled.li`
  height: 0;
  width: 32%;
`;

const renderProjects = () => {
  const projects = [
    {
      name: 'Rideaway',
      description:
        'A routeplanner aiming to improve the experience of bikers in Brussels. 🚴',
      colorPrimary: '#fbba00',
      colorSecondary: '#fff',
      link: 'http://routeplanner.bike.brussels',
      git: 'https://github.com/oSoc17/rideaway-frontend'
    },
    {
      name: 'Vouddit',
      description: 'A video browser for reddit built using React-Redux. ⚛️',
      colorPrimary: '#151516',
      colorSecondary: '#fff',
      link: 'https://vouddit.com',
      git: 'https://github.com/ArnaudWeyts/Vouddit'
    },
    {
      name: 'iRail Datawall',
      description: 'The iRail data wall at De Krook library in Ghent. 🚃',
      colorPrimary: '#c91f1b',
      colorSecondary: '#fff',
      link: 'https://datawall.herokuapp.com',
      git: 'https://github.com/iRail/datawall'
    },
    {
      name: 'Monumentrun',
      description:
        'Hackathon creation to discover monuments whilst keeping in shape. 🏃',
      colorPrimary: '#C95442',
      colorSecondary: '#fff',
      link: 'https://monumentrun.org',
      git: 'https://github.com/Haroenv/monumentrun-web'
    }
  ];

  return projects.map(p => {
    return (
      <Project key={p.name}>
        <ProjectContainer color={p.colorPrimary}>
          <Title color={p.colorSecondary}>{p.name}</Title>
          <Text>{p.description}</Text>
        </ProjectContainer>
        <Actions color={p.colorPrimary}>
          <a href={p.link}>View</a>
          <a href={p.git}>GitHub</a>
        </Actions>
      </Project>
    );
  });
};

const Projects = () => {
  return (
    <Container>
      {renderProjects()}
      <Filler />
      <Filler />
    </Container>
  );
};

export default Projects;



